import React from "react";
import ReactDOM from "react-dom";
import "./NewPartnerForm.css"
import Grid from "@mui/material/Grid";
import FormControl from "@mui/material/FormControl";
import TextField from "@material-ui/core/TextField";
import YitComponent from "../../../Structures/YitComponent";
import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import YitForm from "../../../Components/YitForm/YitForm";
import InputLabel from "@mui/material/InputLabel";
import YitFragment from "../../../Components/YitFragment/YitFragment";
import {GoogleMap, LoadScript, Marker} from "@react-google-maps/api";
import PartnerTypes from "../../../Structures/PartnerTypes";
import PartnerStates from "../../../Structures/PartnerStates";
import LocationSearchModal from "../../../Components/LocationSearchModal/LocationSearchModal";
import YitLocationPicker from "../../../Components/YitLocationPicker";
import {MAP_API_KEY} from "../../../Apis/GlobalConfig";


const DoneMsg = () => {
    return (<div className="success-animation">
        <svg className="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
            <circle className="checkmark__circle" cx="26" cy="26" r="25" fill="none"/>
            <path className="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
        </svg>
        <div>Station submitted successfully</div>
    </div>)
}

class NewPartnerForm extends YitComponent {
    constructor(props) {
        super(props);
        this.state = {
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            partnersList: [],
            markers: []
        }

        this.stationTypes = [
            {
                label: "active",
                value: 0
            },
            {
                label: "inactive",
                value: 1
            }
        ]
        this.formRef = React.createRef()
    }

    async componentDidMount() {
        let getOp = await this.MODELS._Partner.getAll(0, 999)
        if (getOp.finalResult !== false) {
            let partners = getOp.result.rows
            let prepared = partners.map(partner => {
                return {value: partner.id, label: partner.fullName}
            })
            this.setState({
                partnersList: prepared
            })
            let password = this.generatePassword()
            this.setState({suggestedPassword: "password"})

        } else {

        }
    }

    generatePassword(){
        let chars = "0123456789abcdefghijklmnopqrstuvwxyz!@#$%^&*()ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        let passwordLength = 8;
        let password = "";
        for (let i = 0; i <= passwordLength; i++) {
            let randomNumber = Math.floor(Math.random() * chars.length);
            password += chars.substring(randomNumber, randomNumber +1);
        }
        return password
    }

    async handleSubmit(newData){

        let createOp = await this.MODELS._Partner.create(newData)
        console.log(createOp)
        if (createOp.finalResult) {
            return true
        } else {

            this.formRef.current.setState({
                errMsg: createOp.error
            })
            return false
        }
    }

    render() {
        return (
            <div className={'NewPartnerForm'}>
                <YitForm
                    ref={this.formRef}
                    handleSubmit={this.handleSubmit.bind(this)}
                >
                    <Grid container spacing={2} xs={12}>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                id="outlined-required" name={'fullName'} label="Full Name"
                            >
                            </TextField>
                        </Grid>
                        <Grid item xs={3}>
                            <FormControl fullWidth>
                                <InputLabel >Rent Policy</InputLabel>
                                <Select

                                    id="demo-simple-select"
                                    name={"type"}
                                    label="Rent Policy"
                                    defaultValue={-1}
                                >
                                    <MenuItem value={-1}>Select Policy</MenuItem>
                                    {PartnerTypes.map((option) => (
                                        <MenuItem value={option.id}> {option.label}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={3}>
                            <FormControl fullWidth>
                                <InputLabel >Stat</InputLabel>
                                <Select

                                    id="demo-simple-select"
                                    name={"stat"}
                                    label="State"
                                    defaultValue={-1}
                                >
                                    <MenuItem value={-1}>Select Stat</MenuItem>
                                    {PartnerStates.map((option) => (
                                        <MenuItem value={option.id}> {option.label}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={5}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                name={"phone"} id="outlined-required"
                                label="Phone" placeholder={"0X XX XX XX XX"}
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                name={"mail"} id="outlined-required"
                                label="Address mail" placeholder={"xxx@xxx.xxx"}
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                name={"password"} id="outlined-required"
                                defaultValue={this.generatePassword()}
                                label="Password" placeholder={""}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadScript
                                googleMapsApiKey={MAP_API_KEY}
                            >

                                <YitLocationPicker/>
                            </LoadScript>
                        </Grid>

                    </Grid>
                </YitForm>
            </div>
        )
    }
}

export default NewPartnerForm