import React from 'react';
import "./YitPage.css"
import CancelIcon from '@mui/icons-material/Cancel';
import ReactDOM from "react-dom";
import YitComponent from "../../Structures/YitComponent";
import CircularProgress from "@mui/material/CircularProgress";
import Button from "@material-ui/core/Button";
import ReplayIcon from '@mui/icons-material/Replay';
import IconButton from "@mui/material/IconButton";
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import Alert from "@mui/material/Alert";
import {Close} from "@mui/icons-material";
import NewStationForm from "../../Pages/Stations/NewStationForm/NewStationForm";
class YitPage extends YitComponent {
    static id=0
    constructor(props){
        super(props);
        this.state = {
            isLoading: true,
            failedLoading: false,
            notifications: new Map(),

        }
        this.pageId = 'YitPag'+YitPage.id
        this.backDropId  = "YitPageBackDrop"+YitPage.id
        this.backDropContentId = "YitPageBackDropContent"+YitPage.id
        this.notificationsContainerId = "notificationsContainer"+YitPage.id
        this.notId = 0;
        YitPage.id++
    }

    componentWillUnmount() {

    }

    pushNotification(Msg, severity, duration){
        let id=this.notId
        this.state.notifications.set(
            id,
            <Alert
                id={id}
                severity={severity}
                sx={{mb: 2}}
                action={
                    <IconButton
                        aria-label="close"
                        color="inherit"
                        size="small"
                        onClick={() => {
                            this.state.notifications.delete(id)
                            this.setState({notifications: this.state.notifications})
                        }}
                    >
                        <Close fontSize="inherit" />
                    </IconButton>
                }
            >
                {Msg}
            </Alert>
        )
        this.notId++
        this.setState({notifications: this.state.notifications})
        if(duration !== undefined){
            setTimeout(()=>{
                this.state.notifications.delete(id)
                this.setState({notifications: this.state.notifications})
            }, duration)
        }
    }

    async componentDidMount() {
        
    }

    mountBackDrop(component){
        document.getElementById(this.backDropId).addEventListener('click', ()=>{
            ReactDOM.unmountComponentAtNode(document.getElementById(this.backDropContentId))
            document.getElementById(this.backDropId).style.display = "none"
        })
        document.getElementById(this.backDropContentId).addEventListener('click', (event)=>{
            event.stopPropagation();
        })
        document.getElementById(this.backDropId).style.display = "flex"
        ReactDOM.render(component, document.getElementById(this.backDropContentId))
    }

    unmount(component){
        document.getElementById(this.backDropId).style.display = "none"
       // ReactDOM.render(component, document.getElementById("BackDropContent"))
    }

    drawOperations(){
        if(this.props.topOperations !== undefined && this.props.topOperations.length > 0){
            return (
                <>
                    <div className="entriesContainer" id={"entriesContainer"}>
                        {this.props.topOperations.map((operation, index) => {
                            return (
                                <div
                                    key={index}
                                    className={"operationEntry"}
                                    style={{marginLeft: 10, display: "flex", alignItems:"center"}}
                                    onClick={operation.onClick}
                                >
                                    {operation.icon} {operation.text}
                                </div>
                            )})}
                    </div>
                    <MenuOpenIcon
                        fontSize={"large"} style={{cursor: "pointer"}}
                        onClick={()=>{
                            let selector = document.getElementById("entriesContainer")
                            if (selector.style.visibility === "hidden") {
                                selector.style.visibility = "visible";
                            } else {
                                selector.style.visibility = "hidden";
                            }
                        }}/>
                </>

            )
        }
    }
    drawNotifications(map){
        let list = []
        map.forEach((value, key)=>{
            list.push(value)
        })
        return (list)
    }


    render() {
        if(this.state.failedLoading){
            return (
                <div className={"failedToLoadPage"}>
                    <span>Failed to load page primary data</span>
                    <br/>
                    <Button
                        style={{width: "10vw"}}
                        color={"primary"}
                        variant={"contained"}  startIcon={<ReplayIcon/>}
                        onClick={()=>{
                            try {
                                this.props.reloadPrimaryInfo()
                            }
                            catch (error){

                            }
                        }}
                    >
                        retry
                    </Button>
                </div>)
        }
        else {
            if(this.state.isLoading){
                return (<div className={"loadingPage"}><CircularProgress /></div>)
            }
            else {
                return (
                    <section className={'YitPage'} id={this.pageId}>
                        <div className={"YitPageBackDrop"} id={this.backDropId}>
                            <div className="YitPageBackDropContent" id={this.backDropContentId}></div>
                        </div>
                        <div className="YitPageNotificationsContainer" id={this.notificationsContainerId}>
                            {this.drawNotifications(this.state.notifications)}
                        </div>
                        <div className={`YitPageMainPage ${this.props.customClass}`} id={this.props.customId}>
                            <div className="pagesTitle" >{this.props.title}</div>
                            <div className="pagesOperations">
                                {this.drawOperations()}
                            </div>
                            <div className={`pagesContent ${this.props.customContentClass}`} id={this.props.contentCustomId}>
                                {this.props.children}
                            </div>
                        </div>
                    </section>
                )
            }
        }

    }
}

export default YitPage