import {SERVER, ACTOR} from "../Apis/GlobalConfig";
import RequestHandler from "../Apis/RequestHandler";
import _Model from "./_Model";
const MODEL = "Partner/"
const MAIN_ENTRY = SERVER+ACTOR+MODEL
class _Partner extends _Model{

    constructor(adminToken) {
        super(adminToken, MODEL)
        this.adminToken =  adminToken
    }
    
    update = async(id, postData) =>{
        const URL = MAIN_ENTRY+"update/"+id
        let result = await RequestHandler.POST(URL, postData, this.adminToken)
        return result
    }

    addImages = async(id, images) =>{
        const URL = MAIN_ENTRY+"addImages/"+id
        let result = await RequestHandler.POST(URL, {images: images}, this.adminToken)
        return result
    }

    deleteImage = async(id) =>{
        const URL = MAIN_ENTRY+"deleteImages/"+id
        let result = await RequestHandler.GET(URL, this.adminToken)
        return result
    }


}

export default _Partner;