import React from "react";
import ReactDOM from "react-dom";
import "./PartnerEditForm.css"
import Grid from "@mui/material/Grid";
import FormControl from "@mui/material/FormControl";
import TextField from "@material-ui/core/TextField";
import YitComponent from "../../../Structures/YitComponent";
import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import YitForm from "../../../Components/YitForm/YitForm";
import InputLabel from "@mui/material/InputLabel";
import YitFragment from "../../../Components/YitFragment/YitFragment";
import {LoadScript} from "@react-google-maps/api";
import PartnerTypes from "../../../Structures/PartnerTypes";
import PartnerStates from "../../../Structures/PartnerStates";
import YitLocationPicker from "../../../Components/YitLocationPicker";
import {API_KEY} from "../../../Apis/GlobalConfig";


class PartnerEditForm extends YitComponent {
    constructor(props) {


        super(props);
        this.state = {
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            partnersList: [],
            markers: []

        }
        this.fragment = React.createRef()


        this.stationTypes = [
            {
                label: "active",
                value: 0
            },
            {
                label: "inactive",
                value: 1
            }
        ]
        this.formRef = React.createRef()
    }

    async componentDidMount() {
        let getOp = await this.MODELS._Partner.getAll(0, 999)
        if (getOp.finalResult !== false) {
            let partners = getOp.result.rows
            let prepared = partners.map(partner => {
                return {value: partner.id, label: partner.fullName}
            })
            this.setState({
                partnersList: prepared
            })
        } else {

        }
    }


    async handleSubmit(newData) {
        let updateOp = await this.MODELS._Partner.update(this.props.partner.id, newData)
        if (updateOp.finalResult) {
            this.formRef.current.setState({
                doneMsg: updateOp.result
            })
            return true
        } else {

            this.formRef.current.setState({
                errMsg: updateOp.error
            })
            return false
        }
    }

    render() {
        return (
            <YitFragment
                ref={this.fragment}
                customClass={"PartnerEditForm"}
                customId={"PartnerEditForm"}
                isLoading={false}
                primaryInfoLoader={() => this.loadPrimaryData().then()}
            >
                <YitForm
                    ref={this.formRef}
                    handleSubmit={this.handleSubmit.bind(this)}
                    onSuccess={()=>{
                        window.location.reload()
                    }}
                >
                    <Grid container spacing={2} xs={12}>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                id="outlined-required" name={'fullName'}
                                label="Name" defaultValue={this.props.partner.fullName}
                            >
                            </TextField>
                        </Grid>
                        <Grid item xs={3}>
                            <FormControl fullWidth>
                                <InputLabel >Rent Policy</InputLabel>
                                <Select
                                    variant={"outlined"}
                                    id="demo-simple-select"
                                    name={"type"}
                                    label="Rent Policy"
                                    defaultValue={this.props.partner.type}
                                >
                                    {PartnerTypes.map((option) => (
                                        <MenuItem value={option.id}> {option.label}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={3}>
                            <FormControl fullWidth>
                                <InputLabel >Stat</InputLabel>
                                <Select

                                    id="demo-simple-select"
                                    name={"stat"}
                                    label="State"
                                    defaultValue={this.props.partner.stat}
                                >
                                    {PartnerStates.map((option) => (
                                        <MenuItem value={option.id}> {option.label}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                name={"phone"} id="outlined-required"
                                label="Phone" placeholder={"0X XX XX XX XX"} defaultValue={this.props.partner.phone}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth required variant={"outlined"}
                                name={"mail"} id="outlined-required" defaultValue={this.props.partner.mail}
                                label="Address mail" placeholder={"xxx@xxx.xxx"}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <LoadScript
                                googleMapsApiKey={API_KEY}
                            >
                              <YitLocationPicker center={{lat: this.props.partner.x, lng: this.props.partner.y}}/>
                            </LoadScript>
                        </Grid>
                    </Grid>
                </YitForm>
            </YitFragment>
        )


    }
}

export default PartnerEditForm