import React from "react";
import "../Style/LeftPane.css"
import {Link} from "react-router-dom";
import NavBar from "./NavBar";
import Logo from "./Logo";

import {LOCALSTORAGE} from "../Apis/GlobalConfig";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import Button from "@mui/material/Button";
const MuiIcons = require("@mui/icons-material")


class LeftPane extends React.Component{

    constructor(props){
        super(props);
        this.state ={
            openDialog : false
        }
    }

    componentDidMount() {

    }

    logOut(){
        LOCALSTORAGE.removeItem("adminMail")
        LOCALSTORAGE.removeItem("adminToken")
        window.location.assign("/Login")
    }
    render(){
        return(
            <React.Fragment>
                <div className="LeftPane">
                    <Dialog
                        open={this.state.openDialog}
                        onClose={()=>{

                        }}
                    >

                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                You are about to leave the application
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={this.logOut}>Log Out</Button>
                            <Button onClick={() => {
                                    this.setState({
                                        openDialog : false
                                    })
                                }}>
                                close
                            </Button>
                        </DialogActions>
                    </Dialog>

                    <div className="logoContainer">
                        <Logo/>
                    </div>
                    <div className="navBarContainer">
                        <NavBar/>
                    </div>

                    <span className={"logOutButton"} onClick={
                        () => {
                            this.setState({
                                openDialog : true
                            })
                        } }>
                        <MuiIcons.LogoutOutlined/>
                        <p className={"navText"}>Log Out</p>
                    </span>
                </div>
            </React.Fragment>

        )
    }
}
export default LeftPane