import React from "react";
import "../Style/RightPane.css"
import {Redirect, Route} from "react-router-dom";

import { Switch } from "react-router-dom";

import routes from "../routes";

function switchRoutes (routes) {
    return (
        <Switch>
            {routes.map((route, key) => {
                if((route.parametrised === undefined) || (route.parametrised) === false){
                    return (<Route key={key} exact path={route.path}  component={route.component}></Route>)
                }else {
                    return(
                        <Route key={key} exact path={route.path} render={(props) => {
                            return (<route.component id={props.match.params.id}/>);
                        }}>
                        </Route>
                    )
                }
            })}
            <Redirect from={"/"} to={"/DashBoard/Dashboard"}></Redirect>
        </Switch>
    )
};

class RightPane extends React.Component{
    render(){
        return(
            <React.Fragment>
                    <div className="RightPane" id={"RightPane"}>
                        {switchRoutes(routes)}
                    </div>

            </React.Fragment>
        )
    }
}
export default RightPane