import {HeartBit, Internet} from '../Actions/Types';

const initialState = {
    items: [],
    item: {}
};

export default function(state = initialState, action) {
    switch (action.type) {
        case HeartBit:
            return {
                ...state,
                server: action.payLoad
            };
            break;
        case Internet:
            return {
                ...state,
                internet: action.payLoad
            };

        default:
            return state;
    }
}