import React from "react";
import ReactDOM from "react-dom";
import "./CodeGeneratorForm.css"
import Grid from "@mui/material/Grid";
import FormControl from "@mui/material/FormControl";
import TextField from "@material-ui/core/TextField";

import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import YitForm from "../../../Components/YitForm/YitForm";
import InputLabel from "@mui/material/InputLabel";
import RechargeCodeStates from "../../../Structures/RechargeCodeStates";
import YitComponent from "../../../Structures/YitComponent";


const DoneMsg = () => {
    return (<div className="success-animation">
        <svg className="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
            <circle className="checkmark__circle" cx="26" cy="26" r="25" fill="none"/>
            <path className="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
        </svg>
        <div>Station submitted successfully</div>
    </div>)
}


class CodeGeneratorForm extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            partners: []
        }
        this.currentForm = React.createRef()
    }
    async componentDidMount() {

    }
    async  doGenerate(data){
        data["partnerId"] = this.props.partner.id
        let c = await this.MODELS._RechargeCode.create(data)
        if(c.finalResult === true){
            return true
        }else {
            this.currentForm.current.setState({errMsg: JSON.stringify(c.error)})
            return false
        }
    }
    render() {
        return (
            <div className={"CodeGeneratorForm"}>
                <YitForm
                    ref={this.currentForm}
                    errorMsg={"Lot created successfully"}
                    handleSubmit={this.doGenerate.bind(this)}
                >

                        <TextField
                            disabled fullWidth variant={"outlined"}
                            name="partnerId" labelId={"Partner-select-label"} label={"Partner"}
                            defaultValue={this.props.partner.fullName}
                        >

                        </TextField>

                    <br/><br/>
                    <TextField
                        required fullWidth variant={"outlined"}
                        name={"amount"}
                        label="Amount"
                        type={"number"}
                    >
                    </TextField>
                    <br/><br/>
                    <TextField
                        required fullWidth variant={"outlined"}
                        name={"number"} label="Number"
                        type="number"
                    >
                    </TextField>
                    <br/><br/>
                    <FormControl fullWidth>
                        <InputLabel id="algorithm-select-label">Algorithm</InputLabel>
                        <Select
                            fullWidth
                            name="algorithm"
                            label={"Algorithm"}
                            defaultValue={0}
                        >
                            <MenuItem value={0}>bcrypt + JWT</MenuItem>
                        </Select>
                    </FormControl>
                    <br/><br/>
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label">Stat</InputLabel>
                        <Select
                            required
                            fullWidth
                            name={"stat"}
                            labelId="demo-simple-select-label"
                            label="Stat"
                            defaultValue={1}
                        >
                            {RechargeCodeStates.map(cs =>{
                                return       <MenuItem value={cs.value}>{cs.label}</MenuItem>
                            })}
                        </Select>
                    </FormControl>
                </YitForm>
            </div>


        )
    }
}
export default CodeGeneratorForm