import React from "react";
import "./YitImageSlider.css"
import Button from "@material-ui/core/Button";
import IconButton from '@mui/material/IconButton';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import {Delete} from "@mui/icons-material";
import {CircularProgress} from "@material-ui/core";
import {Notifications} from "../../Apis/Notifications";


class Image extends React.Component{
    constructor(props) {
        super(props);
        this.container = {
            width: 200,
            height: 200,
            margin: "10px",
            borderRadius: 20,
            position: "relative"
        }
        this.imageStyle = {
            width: "100%",
            height: "100%",
            borderRadius: 20
        }
        this.deleteImage= {

        }
        this.state ={

        }

    }
    componentDidMount() {
        document.getElementById(this.props.id).onmouseenter = ()=>{
            document.getElementById("delete"+this.props.id).style.display = "flex"
        }
        document.getElementById(this.props.id).onmouseleave = ()=>{
            document.getElementById("delete"+this.props.id).style.display = "none"
        }
    }

    render() {
        return(
            <div style={this.container}  id={this.props.id}>
                <img className={"image"} src={this.props.url} />
                <div
                    className="deleteImage" style={this.deleteImage} id={"delete"+this.props.id}
                    onClick={()=>this.props.onDelete(this.props.systemId)}
                >
                    <Delete style={{color: 'red'}}></Delete>
                </div>
            </div>
        )
    }
}

class NewImage extends React.Component{
    constructor(props) {
        super(props);
        this.container = {
            width: 200,
            height: 200,
            margin: "10px",
            borderRadius: 20,
            position: "relative"
        }
        this.imageStyle = {
            width: "100%",
            height: "100%",
            borderRadius: 20
        }
        this.deleteImage= {

        }
        this.state ={
            uploading: false
        }

    }
    componentDidMount() {
        document.getElementById("containerNew"+ this.props.id).onmouseenter = ()=>{
            document.getElementById("delete"+this.props.id).style.display = "flex"
            document.getElementById("new"+this.props.id).style.display = "flex"
        }
        document.getElementById('containerNew' +this.props.id).onmouseleave = ()=>{
            document.getElementById("delete"+this.props.id).style.display = "none"
            document.getElementById("new"+this.props.id).style.display = "none"
        }

    }
    isUploading(uploading){
        if(uploading){
            return (<CircularProgress />)
        }

    }
    render() {
        return(
            <div style={this.container}  id={"containerNew"+this.props.id} >
                <img className={"image"} src={this.props.url} />
                <div className="deleteImage" id={"delete"+this.props.id} onClick={this.props.onClick.bind(this)}>
                    <Delete style={{color: 'red'}}></Delete>
                </div>
                <div className="newLabel" style={this.deleteImage} id={"new"+this.props.id}>
                    New
                </div>

            </div>
        )
    }
}

class YitImageSlider extends React.Component {
    constructor(props) {
        super(props);
        this.sharedData = {}
        this.state = {
            working: false,
            currentImage: '',
            done: false,
            partnersList: [],
            markers: [],
            propsImages: this.props["ImagesList"] ?? [],
            toUpload: [],
            toBeUploaded: [],
            ImagesList : []
        }
        this.state.propsImages.forEach((image, index) =>{
            this.state.ImagesList.push({id: image.id, link: image.link, toUpload : false})
        })

        this.stationTypes = [
            {
                label: "active",
                value: 0
            },
            {
                label: "inactive",
                value: 1
            }
        ]
        this.formRef = React.createRef()
    }

    async componentDidMount() {
        this.setState({
            ImagesList: this.state.ImagesList
        })
    }

    isCompleted(){
        if(this.sharedData !== undefined && this.sharedData !== {}){
            return true
        }else {
            alert("no images selected")
            return false
        }
    }

    onImageSelect(e){
        for (let i = 0; i < e.target.files.length; i++){
            const file = e.target.files[i];
            console.log(file)
            let imageLink =  URL.createObjectURL(file)
            this.state.ImagesList.push({link: imageLink, toUpload: true, file: file})

        }

        // let imageTag  = document.getElementById('imageTag')
        this.setState({
            ImagesList:  this.state.ImagesList
        })
        this.sharedData = this.state.toUpload
    }

    async handleSubmit(newData){
        let password = this.generatePassword()
        newData['password'] = password
        let createOp = await this.MODELS._Partner.create(newData)
        if (createOp.finalResult) {
            return true
        } else {

            this.formRef.current.setState({
                errMsg: createOp.error
            })
            return false
        }
    }

    deleteToBeUploaded(image){
        let newImages = []
        this.state.ImagesList.forEach(imagee =>{
            if(image.link !== imagee.link) newImages.push(imagee)
        })
        this.setState({
            ImagesList: newImages
        })
        // const index = this.state.ImagesList.indexOf(image);
        // this.setState({
        //     ImagesList: this.state.ImagesList
        // })
        // alert(index)
        // alert(JSON.stringify(this.state.ImagesList))
    }

    deleteAlreadyUploaded(id){
        this.props.onDelete([{id: id}])
    }

    renderContent(list){
        return(
        list.map((image, index) =>{
            if(image.toUpload){
                return (
                    <NewImage
                        url={image.link} id={index} ref={image}
                        onClick={() =>this.deleteToBeUploaded({link: image.link, toUpload: true, file: image.file})}>

                    </NewImage>)
            }else {
                return (
                    <Image
                        url={image.link}
                        id={index}
                        systemId={image.id}
                        onDelete={this.deleteAlreadyUploaded.bind(this)}
                    />
                )
            }

        }))
    }

    async onSave(){

        this.setState({working: true})
        let toUpload = []
        for (const image of this.state.ImagesList) {
            if (image.toUpload) {
                toUpload.push(image)
            }
        }
        let result = await this.props.onSave(toUpload)
        if(result){
            Notifications.drawActionResult("notificationsContainer1", "Images uploaded ad attached to partner", "success")
            this.setState({working: false})
        }else {
            Notifications.drawActionResult("notificationsContainer1", "Failed to finish the operation ", "error")

            this.setState({working: false})
        }


    }

    renderGlobalOperations(list){

        let show = false
        for (const image of list) {
            if(image.toUpload){


                show = true
                break;
            }
        }
        if(show){
            return (<div>
                <Button
                    variant={"contained"} color={"primary"}
                    onClick={this.onSave.bind(this)}
                >
                    Upload
                </Button>
            </div>)
        }
    }

    render() {
        if(this.state.working){
            return (
                <div className={'YitImageSlider'}><CircularProgress></CircularProgress></div>
            )
        }else{
            return (
                <div className={'YitImageSlider'}>
                    <div className="Content">
                        <div className={"addImages"}  onClick={()=>{document.getElementById("fileInput").click()}}>
                            <IconButton aria-label="delete" size={"large"}>
                                <AddCircleOutlineIcon fontSize="inherit" >

                                </AddCircleOutlineIcon>
                                <input
                                    id={"fileInput"}
                                    onChange={(e)=>this.onImageSelect(e)}
                                    type="file"
                                    hidden
                                    multiple
                                />
                            </IconButton>
                        </div>
                        {this.renderContent(this.state.ImagesList)}
                    </div>
                    {this.renderGlobalOperations(this.state.ImagesList)}
                </div>
            )
        }

    }
}

export default YitImageSlider