import React from 'react';
import './YitDataTable.css'
import MaterialTable, {MTableToolbar, MTablePagination} from '@material-table/core';
import tableIcons from "../tableIcons"
import Button from "@material-ui/core/Button";


class YitDataTable extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            page: 0,
            totalCount: 0,
            searchEntry: this.props.searchEntry !== undefined ? this.props.searchEntry : "id",
            options: {
                search: true,
                actionsColumnIndex: -1,
                exportButton: true,
                paginationPosition: "top",
                pageSizeOptions: [5, 10, 20, 50, 0],
            }
        }
        const urlParams = new URLSearchParams(window.location.search);
        this.tableRef = React.createRef();

    }

    async componentDidMount() {
        if(this.props.options !== undefined){
            let newOptions = this.state.options
            for (const option in this.props.options) {
                newOptions[option] = this.props.options[option]
            }
            this.setState({options : newOptions})
        }
    }

    async loadTotal(){

    }

    async loadData(query){
        let offset = query.page*query.pageSize, limit =  query.pageSize
        let getAllOp

        if(query.search === ""){
            getAllOp = await this.props.dataModel.getAll(offset, limit)
        }
        else{

            getAllOp = await this.props.dataModel.searchBy(this.state.searchEntry, query.search)
        }

        if(getAllOp.finalResult){
            let result = {
                data : getAllOp.result.rows,
                total: getAllOp.result.count
            }
            if(query.filters.length > 0){

                let filters = query.filters
                let r = []
                result.data.forEach(entry =>{
                    let accepted = true
                    for (let filter of filters){
                        let filterCol = filter["column"]["field"]
                        let filterValue = filter["value"]
                        if(entry[filterCol] !=   filterValue) {
                            accepted = false
                        }

                    }
                    if(accepted){
                        r.push(entry)
                    }

                })
                console.log(r)
                result.data = r
            }
            return result
        }else {
            return false
        }
    }

    componentWillUnmount() {

    }

    render() {
        return (
            <MaterialTable
                {...this.props}
                style={{ minHeight: "100%", fontFamily: "Poppins"}}
                tableRef={this.tableRef}
                icons={tableIcons}
                title={""}
                components={{
                    Container: props => <div
                        style={{
                            overflow: "auto", position: 'relative',
                            background: '#FFFFFF', width:"100%", height:"100%"}}>{props.children}</div>
                }}
                data={query => new Promise((resolve, reject) => {
                        this.loadData(query)
                            .then(result => {
                                if(result == false){
                                    reject()
                                }else {
                                    resolve({
                                        data: result['data'],
                                        page: query.page,
                                        totalCount: result['total'],
                                    })
                                }

                            })
                    })}
                options={this.state.options}
                localization={{
                    pagination: {
                        labelDisplayedRows: '{from}-{to} of {count}'
                    },
                    toolbar: {
                        nRowsSelected: '{0} row(s) selected'
                    },
                    header: {
                        actions: 'Actions'
                    },
                    body: {
                        emptyDataSourceMessage: 'No records to display',
                        filterRow: {
                            filterTooltip: 'Filter'
                        }
                    },
                    error:
                        <div className={"errorContainer"}>
                            <span>Data could not be Loaded</span>
                            <br/><br/>
                            <Button
                                style={{backgroundColor: "red", opacity: 20}}
                                color={"primary"}
                                variant={"contained"}
                                onClick={()=>{
                                    this.tableRef.current.onQueryChange({});
                                }}
                            >
                                Retry
                            </Button>
                        </div>
                }}
            >
            </MaterialTable>
        )
    }
}

export default YitDataTable