import React from "react";
import {render} from "react-dom";
import "../Style/Logo.css"
import logoImage from "../Imgs/logo.svg"

class Logo extends React.Component{

    constructor(props){
        super(props);
    }
    render(){
        return(
            <React.Fragment>
                <img className={"Logo"} src={logoImage}/>
            </React.Fragment>

        )
    }
}


export default Logo