import React from 'react';
import "./YitStepper.css"
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import YitComponent from "../../Structures/YitComponent";
import YitStep from "./YitStep";



class YitStepper extends YitComponent {
    constructor(props) {
        super(props);
        this.sharedData = []
        this.state = {
            activeStep: 0
        }
        this.currentStep = React.createRef()
    }


    isStepOptional = (step) => {
        return this.props.steps[step].optional;
    };

    isStepSkipped = (step) => {
        return false;
    };

    handleNext = () => {
        if(this.currentStep.current.isCompleted()){
            this.sharedData[this.state.activeStep] = this.currentStep.current.sharedData
            this.setActiveStep(this.state.activeStep)
            alert(JSON.stringify(this.sharedData))
        }
        else {

        }
    };

     handleBack = () => {
         this.setState({
             activeStep: this.state.activeStep -1
         })
    };
    setActiveStep = (activeStep)=>{

        this.setState({
            activeStep: activeStep + 1
        })
    }
    handleSkip = () => {

        if (!this.isStepOptional(this.state.activeStep)) {
            // You probably want to guard against something like this,
            // it should never occur unless someone's actively trying to break something.
            throw new Error("You can't skip a step that isn't optional.");
        }

        this.setActiveStep(this.state.activeStep);

    };

    handleReset = () => {
        this.setState({
            activeStep: 0
        })
    };

    setContent = (activeStep)=>{
        if(this.state.activeStep === this.props.steps.length ){
            let  childProps = {ImagesList: this.sharedData[1]};
            return React.cloneElement(this.props.onFinishView, childProps);
        }else {
            try{
                let  childProps = {ref: this.currentStep, a:5};
                return React.cloneElement(this.props.children[activeStep], childProps);
            }
            catch (e){

            }
        }


    }

    render() {
        return (
            <div className={'YitStepper'}>
                <div className="header">
                    <Stepper activeStep={this.state.activeStep}>
                        {this.props.steps.map((step, index) => {
                            const stepProps = {};
                            const labelProps = {};
                            if (this.isStepOptional(index)) {
                                labelProps.optional = (
                                    <Typography variant="caption">Optional</Typography>
                                );
                            }
                            if (this.isStepSkipped(index)) {
                                stepProps.completed = false;
                            }
                            return (
                                <Step key={step} {...stepProps}>
                                    <StepLabel {...labelProps}>{step.label}</StepLabel>
                                </Step>
                            );
                        })}
                    </Stepper>
                </div>
                <div className="content">
                    {this.setContent(this.state.activeStep)}
                </div>
                <div className="operations">
                    {this.state.activeStep === this.props.steps.length ? (
                        <React.Fragment>
                            <Typography sx={{ mt: 2, mb: 1 }}>
                                All steps completed - you&apos;re finished
                            </Typography>
                            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                                <Box sx={{ flex: '1 1 auto' }} />
                                <Button onClick={this.handleReset}>Reset</Button>
                            </Box>
                        </React.Fragment>
                    ) : (
                        <React.Fragment>

                            <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                                <Button
                                    color="inherit"
                                    disabled={this.state.activeStep === 0}
                                    onClick={this.handleBack}
                                    sx={{ mr: 1 }}
                                >
                                    Back
                                </Button>
                                <Box sx={{ flex: '1 1 auto' }} />
                                {this.isStepOptional(this.state.activeStep) && (
                                    <Button color="inherit" onClick={this.handleSkip} sx={{ mr: 1 }}>
                                        Skip
                                    </Button>
                                )}

                                <Button onClick={this.handleNext}>
                                    {this.state.activeStep === this.props.steps.length - 1 ? 'Finish' : 'Next'}
                                </Button>
                            </Box>
                        </React.Fragment>
                    )}
                </div>
            </div>


        );
    }
}

export default YitStepper;