import {SERVER, ACTOR} from "../Apis/GlobalConfig";
import RequestHandler from "../Apis/RequestHandler";
import _Model from "./_Model";
const MODEL = "Client/"
const MAIN_ENTRY = SERVER+ACTOR+MODEL
class _Client extends _Model{

    constructor(adminToken) {
        super(adminToken, MODEL)
        this.adminToken =  adminToken
    }

    create = async(postData) =>{
        const URL = MAIN_ENTRY+"getAll/0/99999"
        let result = await RequestHandler.POST(URL, postData)
        if(result.finalResult){
            return result.result
        }else {
            return result
        }
    }


    async getOnById(id) {
        const URL = MAIN_ENTRY+"getOne/"+id
        const requestOptions = {
            method: 'GET',
            headers: {'Content-Type': 'application/json'},
        };
        const response = await fetch(URL, requestOptions);
        const data = await response.json();
        if (data.finalResult == true) {
            return data.result;
        } else {
            return null;
        }
    }

}

export default _Client;