import RequestHandler from "../Apis/RequestHandler";
import React from "react";
import _Model from "./_Model";
const MODEL = "RentTransactions/"

class _Transaction extends _Model{
    constructor(adminToken) {
        super(adminToken, MODEL)
    }

    async getAll(operation, offset, limit) {
        const URL = this.MAIN_ENTRY+"getAll/"+operation+"/"+offset+"/"+limit
        let result = await RequestHandler.GET(URL, this.adminToken)
        return result
    }
}

export default _Transaction;