function statToString(stat){
    let result = ""
    RechargeCodeStates.forEach((reCodeState)=>{
        if(reCodeState.value == stat){
            result = reCodeState.label
        }
    })
    return result
}

class ReCodeGeneratorForm extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            partners: []
        }
    }
    async componentDidMount() {
        let partners = await this.props.getPartners(0, 999)
        let prepared = partners.map(partner => {

            return {value: partner.id, label: partner.fullName}
        })
        this.setState({
            partners : prepared
        })
    }
    render() {
        return (
            <YitForm
                errorMsg={"Lot created successfully"}
                handleSubmit={this.props.handleSubmit}>
                <FormControl fullWidth>
                    <InputLabel id="Partner-select-label">Partner</InputLabel>
                    <Select
                        fullWidth
                        name="partnerId"
                        labelId={"Partner-select-label"}
                        label={"Partner"}
                    >
                        {this.state.partners.map(partner =>{
                            return   <MenuItem value={partner.value}>{partner.label}</MenuItem>
                        })
                        }
                    </Select>
                </FormControl>
                <br/><br/>
                <TextField
                    required
                    fullWidth
                    name={"amount"}
                    label="Amount"
                    size="small"
                    type={"number"}
                >
                </TextField>
                <br/><br/>
                <TextField
                    required
                    fullWidth
                    name={"number"}
                    label="Number"
                    size="small"
                    type="number"
                >
                </TextField>
                <br/><br/>
                <FormControl fullWidth>
                    <InputLabel id="algorithm-select-label">Algorithm</InputLabel>
                    <Select
                        fullWidth
                        name="algorithm"
                        label={"Algorithm"}
                    >
                        <MenuItem value={10}>MD5 + JWT</MenuItem>
                        <MenuItem value={20}>Bcrypt Only</MenuItem>
                        <MenuItem value={30}>Bcrypt + JWT</MenuItem>
                    </Select>
                </FormControl>
                <br/><br/>
                <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Stat</InputLabel>
                    <Select
                        required
                        fullWidth
                        name={"stat"}
                        labelId="demo-simple-select-label"
                        label="Stat"
                    >
                        {RechargeCodeStates.map(cs =>{
                            return       <MenuItem value={cs.value}>{cs.label}</MenuItem>
                        })}
                    </Select>
                </FormControl>
            </YitForm>

        )
    }
}

