class Transactions extends YitComponent {

    constructor(props){
        super(props);

        this.state = {
            rawData: [],
            columns: [
                {title: 'Id', field: "id"},
                {title: 'Operation',   field: "operation", lookup: {0 : "Rent",  1 : "Return"}},

            ],
            data: [],
            selectedRowId: null,
            open: false
        }

        this.submitButton = React.createRef()
    }

    async  doGenerate(data){
        let c = await this.MODELS._RechargeCode.create(data)
        if(c.finalResult === true){
            return true
        }else {
            return false
        }
    }


    async componentDidMount() {
        //this.submitButton.current.changeState(false)
        this.setTransactionsList(TransactionOperations.Station.rent.id).then()
        this.currentPage.current.setState({
            isLoading: false
        })

    }

    async dataLoader(query, resolve) {
        let rechargeCodes = await this.MODELS._Transaction.getAll(query.pageSize * query.page, query.pageSize);
        console.log(rechargeCodes)
        if (rechargeCodes !== false) {
            resolve({
                data: rechargeCodes,
                page: query.page,
                totalCount: 100
            })
        } else {
            Notifications.drawActionResult("pagesNotifications", "Could not load Stations data", "error")
            resolve({
                data: [],
                page: query.page,
                totalCount: 100
            })
        }
    }

    async loadRentTransactions() {
        let getAllOp = await this.MODELS._Transaction.getAll(0, 0, 100)
        if(getAllOp.finalResult){
            let transactions = getAllOp.result
            await transactions.forEach((transaction, index, transactions)  =>{
                let newEntry = {
                    id: transaction.id,
                    operation: transaction.operation,
                    date: transaction.createdAt.substr(0, 10)
                }
                transaction.MetaData.forEach(entry => {
                    newEntry[entry.dataTitle] = entry.dataValue
                })
                transactions[index]  = newEntry
            })
            this.setState({
                columns: [
                    {title: 'Id', field: "id"},
                    {title: 'Operation', field: "operation", lookup: {0: "Rent", 1: "Return"}},
                    {title: 'StationId', field: "stationId", render: rowData => <Link target={"_blank"} to={"/DashBoard/StationsDetails/"+rowData.stationId}>{rowData.stationId}</Link>},
                    {title: 'Client Id', field: "clientId"},
                    {title: 'Power Bank Id', field: "powerBankId"},
                    {title: 'amount', field: "amount"},
                    {title: 'Date', field: 'date'}
                ]
            })
            return transactions
        }else {
            return []
        }
    }

    async loadReturnTransactions() {
        let getAllOp = await this.MODELS._Transaction.getAll(TransactionOperations.Station.return.id, 0, 100)
        if(getAllOp.finalResult){
            let transactions = getAllOp.result
            await transactions.forEach((transaction, index, transactions)  =>{
                let newEntry = {
                    id: transaction.id,
                    operation: transaction.operation,
                    date: transaction.createdAt.substr(0, 10)
                }
                transaction.MetaData.forEach(entry => {
                    newEntry[entry.dataTitle] = entry.dataValue
                })
                transactions[index]  = newEntry
            })
            this.setState({

                columns: [
                    {title: 'Id', field: "id"},
                    {title: 'Operation', field: "operation", lookup: {0: "Rent", 1: "Return"}},
                    {title: 'StationId', field: "stationId", render: rowData => <Link target={"_blank"} to={"/DashBoard/StationsDetails/"+rowData.stationId}>{rowData.stationId}</Link>},
                    {title: 'Client Id', field: "clientId"},
                    {title: 'Power Bank Id', field: "powerBankId"},
                    {title: 'Date', field: 'date'},
                ]
            })
            return transactions
        }else {
            return []
        }
    }

    async loadCartTransactions() {
        let getAllOp = await this.MODELS._Transaction.getAll(11, 0, 100)
        let transactions = getAllOp.result
        await transactions.forEach((transaction, index, transactions)  =>{
            let newEntry = {
                id: transaction.id,
                operation: transaction.operation,
                date: transaction.createdAt.substr(0, 10)
            }
            transaction.MetaData.forEach(entry => {
                newEntry[entry.dataTitle] = entry.dataValue
            })
            transactions[index]  = newEntry
        })
        this.setState({
            columns: [
                {title: 'Id', field: "id"},
                {
                    title: 'Operation', field: "operation",
                    lookup: {0: "Rent", 1: "Return", 11 : "recharge"}
                },
                {title: 'Client Id', field: "clientId"},
                {title: 'Recharge code ID', field: "rechargeCodeId"},
                {title: 'Date', field: "date"},
            ]
        })
        return transactions

    }

    async setTransactionsList(type){
        let transactionList
        switch (type) {
            case TransactionOperations.Station.rent.id:
                transactionList = await this.loadRentTransactions()
                break;
            case TransactionOperations.Station.return.id:
                transactionList = await this.loadReturnTransactions()
                break;
            case TransactionOperations.wallet.recharge.id:
                transactionList = await this.loadCartTransactions()
        }
        if(typeof transactionList === "object"){
            this.setState({
                    data: transactionList
                }
            )
        }else {
            this.setState({ data: [] })
        }
    }

    async tabClickHandler(e) {
        this.setTransactionsList(e.target.id).then()
    }

    render() {
        return (
                <YitPage
                    ref={this.currentPage}
                    customClass={"TransactionsPage"} customId={"TransactionsPage"} title={"Transactions"}
                    customContentClass={"TransactionsPageContent"}>
                        <MaterialTable
                            icons={tableIcons}
                            title={''}
                            components={{
                                Toolbar: props => {
                                    return  (
                                        <div className={"TableHeader"}>
                                            <div className={"tableHeaderTabsContainer"}>
                                                <span
                                                    className={"tableTab"}
                                                    onClick={(e)=>{
                                                        this.tabClickHandler(e)
                                                    }}
                                                    id={TransactionOperations.Station.rent.id}
                                                >
                                                    Rent Transactions
                                                </span>
                                                <span
                                                    className={"tableTab"}
                                                    onClick={(e)=>{
                                                        this.tabClickHandler(e)
                                                    }}
                                                    id={TransactionOperations.Station.return.id}
                                                >
                                                    Return Transactions
                                                </span>
                                                <span
                                                    className={"tableTab"}
                                                    onClick={this.tabClickHandler.bind(this)}
                                                    id={TransactionOperations.wallet.recharge.id}
                                                >
                                                    Card Transactions
                                                </span>
                                            </div>
                                            <MTableToolbar {...props} />
                                        </div>
                                    )},

                                Container: props => <div
                                    style={{
                                        overflow: "auto",
                                        background: '#FFFFFF', width:"100%", height:"100%"}}>{props.children}</div>
                            }}
                            columns={this.state.columns}
                            data={this.state.data}
                            //onRowClick={(event, rowData, togglePanel) => togglePanel()}
                            options={{
                                pageSize: 5,
                                actionsColumnIndex: -1,
                                showTitle: false,
                                grouping: true,
                                exportButton: true
                            }}
                            actions={[
                                {
                                    icon: () => <MuiIcons.PersonAdd />,
                                    tooltip: 'Add User',
                                    isFreeAction: true,
                                    onClick: (event, rowData, props) => {
                                        console.log(props)
                                        const rows = [
                                            ["name1", "city1", "some other info"],
                                            ["name2", "city2", "more info"]
                                        ];

                                        let csvContent = "data:text/csv;charset=utf-8,";

                                        rows.forEach(function(rowArray) {
                                            let row = rowArray.join(",");
                                            csvContent += row + "\r\n";
                                        });

                                        let encodedUri = encodeURI(csvContent);
                                        window.open(encodedUri);
                                    },
                                    render: "Hello"
                                },
                                (rowData) => {
                                    switch (rowData.stat ){
                                        case 0:
                                            return {
                                                icon: () =><MuiIcons.Check style={{color: "blue"}}></MuiIcons.Check>,
                                                onClick: (event, rowData) => this.changeStat(rowData, 1),
                                                tooltip: "validate",
                                            }
                                            break
                                        case 1:
                                            return {
                                                icon: () =><MuiIcons.Block style={{color: "red"}}></MuiIcons.Block>,
                                                tooltip: "Freeze User",
                                                onClick: (event, rowData) => this.changeStat(rowData, 2)
                                            }
                                        case 2:
                                            return {
                                                icon: () =><MuiIcons.Beenhere style={{color: "green"}}></MuiIcons.Beenhere>,
                                                tooltip: "Reactivate User",
                                                onClick: (event, rowData) => this.changeStat(rowData, 1)
                                            }
                                    }

                                },


                                (rowData) => {
                                    return {
                                        icon: () =><Link to={{
                                            pathname: "/Dashboard/PartnerDetails/"+rowData.id,
                                            search: '',
                                            state: { data: this.state.rawData[rowData.rowIndex]}
                                        }}><MuiIcons.Launch style={{color: "black"}}/></Link>,
                                        tooltip: "Details",
                                    }
                                }
                            ]}
                        />
                </YitPage>
        )
    }
}

export default Transactions