import {SERVER, ACTOR} from "../Apis/GlobalConfig";
import RequestHandler from "../Apis/RequestHandler";
import _Model from "./_Model";
const MODEL = "Station/"
const MAIN_ENTRY = SERVER+ACTOR+MODEL
class StationModel extends _Model{

    constructor(adminToken) {
        super(adminToken, MODEL)
        this.adminToken =  adminToken
    }

    async getOneByPublicId(id) {
        const URL = MAIN_ENTRY+"getOneByPublicId/"+id
        let result = await RequestHandler.GET(URL, this.adminToken)
        return result
    }

    async getOneRealTime(id) {
        const URL = MAIN_ENTRY+"getRealTimeInfo/"+id
        let result = await RequestHandler.GET(URL, this.adminToken)
        if(result.finalResult){
            return result.data
        }else {
            return false
        }
    }

    async rentPowerBank(id){
        let URL  = MAIN_ENTRY+"rentPowerBank/"+id
        let result = await RequestHandler.GET(URL, this.adminToken)
        return result.finalResult
    }

    async queryAPNNs(stationId, index){
        index = parseInt(index)
        index = "0"+index
        let URL  = MAIN_ENTRY+"queryAPNNs/"+stationId+"/"+index
        let result = await RequestHandler.GET(URL, this.adminToken)
        if(result.finalResult){
            return  result.data
        }else {
            return false
        }
    }

    async setAddress(stationId, data){
        let URL  = MAIN_ENTRY+"setAddress/"+stationId

        let result = await RequestHandler.POST(URL, data, this.adminToken)
        console.log(result)
        return result
    }

    async setVolume(stationId, level){
        level = parseInt(level)
        let URL  = MAIN_ENTRY+"setVolume/"+stationId+"/"+level
        let result = await RequestHandler.GET(URL, this.adminToken)
        return result
    }

}


export default StationModel;