import React from 'react';
import ReactDOM from 'react-dom';
import "./Ads.css"
import DeviceEmulator from 'react-device-emulator';
import 'react-device-emulator/lib/styles/style.css';
import {ClientStats} from "../../Structures/ClientStats";
import {Notifications} from "../../Apis/Notifications";
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import YitDataTable from "../../Components/YitDataTable/YitDataTable";
import NewPartnerForm from "../Partners/NewPartnerForm/NewPartnerForm";
import {Input, MenuItem, Select, TextField} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import IconButton from "@mui/material/IconButton";
import {PhotoCamera} from "@mui/icons-material";
import NewAd from "./NewAd/NewAd";
const MuiIcons = require("@mui/icons-material")



class AdImage extends React.Component{
    static id=0
    constructor(props) {
        super(props);
        this.state= {
            AdImageContainer : 'AdImageContainer'+AdImage.id,
            preImageContainer : 'preImageContainer'+AdImage.id,
            preImage : 'preImage'+AdImage.id
        }
        AdImage.id++
    }

    componentDidMount() {
        let AdImageContainer = document.getElementById(this.state.AdImageContainer)
        let preImageContainer = document.getElementById(this.state.preImageContainer)
        AdImageContainer.addEventListener("mouseenter", ()=>{
            preImageContainer.style.display = "block"
            ReactDOM.render(
                <img className={"preImage"} id={this.state.preImage} src={this.props.image}/>,
                preImageContainer
            )


        })
        AdImageContainer.addEventListener("mouseleave", ()=>{
            ReactDOM.unmountComponentAtNode(preImageContainer)
            preImageContainer.style.display = "none"
        })
    }
    render() {
        return(
            <div className={"AdImageContainer"} id={this.state.AdImageContainer}>
                <span className={"prSpan"}>Preview</span>
                <div className={"preImageContainer"} id={this.state.preImageContainer}></div>
            </div>
        )
    }


}

class Ads extends YitComponent {
    constructor(props) {
        super(props);
        this.state = {
            rawData: [],
            columns: [
                {title: 'ID', field: "id", editable: 'never' },
                {
                    title: 'Image', field: "image",
                    render: rowData => <AdImage image={rowData.image}/>,
                    editComponent: props => {
                        if(props.value !== undefined){
                            return(
                                <div>
                                    <TextField
                                        disabled fullWidth
                                        defaultValue={props.value}>

                                    </TextField>
                                </div>
                            )
                        }
                        else {
                            return(
                                <Select>
                                    <MenuItem value={0}>Upload File</MenuItem>
                                    <MenuItem value={1}>Enter Link</MenuItem>
                                </Select>

                            )
                        }

                    }
                },
                {
                    title: 'Type',   field: "type",
                    lookup: { 0: 'Show to all', 1: 'Vip Only', 2: "Normal Only" },
                },
                {
                    title: 'Stat',   field: "stat",
                    lookup: { 0: 'New', 1: 'Active', 2: "Frozen" },
                },
            ],
            data: [],
            selectedRowId: null,
            open: false
        }
        this.dataTable = React.createRef()
    }

    async updateAd(newData){
        try{
            let updateOp = await this.MODELS._Ad.update(newData.id, newData)
            if(updateOp.finalResult){
                Notifications.drawActionResult("pagesNotifications", "Ad  Updated", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }else{
                Notifications.drawActionResult("pagesNotifications", "Could update Client", "error")
            }
        }catch (e){
            Notifications.drawActionResult("pagesNotifications", "Could not change client info", "error")
        }
    }

    async loadAds() {
        let updateResult = await this.MODELS._Ad.getAll()
        this.setState({
            failedLoading: false,
            data: []
        })

    }

    async loadPrimaryData() {
        await this.loadAds()
        if (this.state.failedLoading) {

            this.currentPage.current.setState({
                failedLoading: true
            })
        } else {
            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }

    }

    async componentWillMount() {
        await this.loadPrimaryData()

    }

    async componentDidMount() {


    }


    async changeStat(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.update(rowData.id, {stat: stat})
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Stat Changed successfully", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }
        } catch (error) {

        }
    }

    async deleteEntry(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.delete(rowData.id)
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Partner Deleted", "success")
                this.dataTable.current.tableRef.current.onQueryChange({})
            }
        } catch (error) {

        }
    }


    render() {
        return (
            <YitPage
                ref={this.currentPage}
                customClass={"ClientsPage"} customId={"ClientsPage"} title={"Advertising"}
                reloadPrimaryInfo={async () => {
                    await this.loadPrimaryData()
                }}
                topOperations={[
                    {
                        text: "New Advertising ",
                        icon: <MuiIcons.Add />,
                        onClick: ()=>{this.currentPage.current.mountBackDrop(<NewAd/>)}
                    }

                ]}
            >


                <YitDataTable
                    ref={this.dataTable}
                    columns={this.state.columns}
                    onRowClick={(event, rowData) => {
                        (event.target).ondblclick = () => {
                            this.props.history.push("/Dashboard/PartnerDetails/" + rowData.id, {  state: {data: this.state.rawData[rowData.rowIndex]}});

                        }
                    }}
                    dataModel={this.MODELS._Ad}
                    searchEntry={'fullName'}
                    actions={[
                        {
                            icon: ()=><MuiIcons.Preview/>,
                            tooltip: 'Preview',
                            onClick: (event, rowData) => {
                                this.currentPage.current.mountBackDrop(
                                    <DeviceEmulator type="mobile" withDeviceSwitch withRotator url="https://nestjs.com/"></DeviceEmulator>
                                )
                            }
                        },
                    ]}

                    editable={{
                        onRowAdd: newData =>
                            new Promise((resolve, reject) => {

                            }),
                        onRowUpdate: (newData, oldData) => this.updateAd(newData),
                        onRowDelete: oldData =>
                            new Promise((resolve, reject) => {
                                this.deleteEntry(oldData).then(r => resolve())
                            })
                    }}


                />
            </YitPage>


        )
    }
}

export default Ads