const PowerBank =  ({PowerBank, clickHandler, id})=>{
    function drawContent(stat){
        if(stat){
            return (
                <Button fullWidth startIcon={<SaveIcon />} variant="contained" color="primary"
                        onClick={()=>{
                            clickHandler(setStat)
                        }}>
                    Eject
                </Button>
            )
        }else {
            return (
                <div>Loading</div>
                // <LoadingButton
                //     fullWidth
                //     loading
                //     loadingPosition="start"
                //     startIcon={<SaveIcon />}
                //     variant="outlined"
                // >
                //     Requesting
                // </LoadingButton>
            )
        }
    }
    const [stat, setStat] = React.useState(true);
    return(
        <div className={"PowerBankView"} id={id} style={{width: 200}}>
        <b>ID: </b>
            {PowerBank.powerBankId}<br/>
            <b>Power Level:</b>{PowerBank.powerLevel}<br/>
            {drawContent(stat)}
        </div>
    )
}

const APNView =  ({APN,  id})=>{
    if(APN != undefined){
        return(
            <div className={"PowerBankView"} id={id} >
                <Grid container spacing={2} xs={8} style={{width: "100%"}}>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="MCCMNC"
                            defaultValue={APN.MCCMNC}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="Name"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={12}>

                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="User Name"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="Password"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                </Grid>
            </div>
        )
    }else {
        return <div>APN information not loaded yet</div>
    }
}

class StationDetails extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            isLoading: true,
            failedLoading : false,
            currentStation: {Partner: {}},
            currentStationInfo: {},
            currentTab :"one",
            stationAPNs : [],
            }
        this.loadPrimaryData().then(r => {})
    }

    async loadPrimaryData(){
        let loadStationOp = await this.loadStationInfo(this.props.id)
        if(loadStationOp){
            await this.loadPowerBanksInfo(this.state.currentStation.id)
            await this.loadAPN(this.state.currentStation.id, 0)
        }else {



        }
    }
    async componentDidMount() {
        if(this.state.failedLoading){
            this.currentPage.current.setState({
                failedLoading: true
            })
        }else {
            this.currentPage.current.setState({
                isLoading: false
            })
        }
    }

    async loadStationInfo(id) {
        let getOneOP = await this.MODELS._Station.getOneByPublicId(id)
        if (getOneOP.finalResult === true) {
            let currentStation = getOneOP.result
            this.setState({
                currentStation: currentStation,
            })
            return true
        } else {
            this.setState({
                failedLoading : true
            })
            return false
        }
    }

    async loadPowerBanksInfo(id) {
        if(id != undefined){
            let currentStationInfo = await this.MODELS._Station.getOneRealTime(id)
            if (currentStationInfo !== false) {
                this.setState({
                    currentStationInfo: currentStationInfo,
                })
            } else {
                Notifications.drawActionResult("pagesNotifications", "Could not Load Station primary  Data", "error")
            }
        }
    }

    async loadAPN(stationId, index) {
        if(stationId != undefined){
            let APN = await this.MODELS._Station.queryAPNNs(stationId, index)
            if(APN !== false){
                let current  = this.state.stationAPNs
                current[index] = APN
                this.setState({
                    stationAPNs: current
                })
            }else {
                Notifications.drawActionResult("pagesNotifications", "Could not Load APN info", "error")
            }
        }
    }

    drawPowerBanks(powerBanksList){
        if(powerBanksList != undefined){
            if(powerBanksList.length > 0){
                return (
                    powerBanksList.map((powerBank, index)=>{
                        return(
                            <div className="infoEntry">
                                <PowerBank
                                    id={"PowerBank"+index}
                                    PowerBank={powerBank}
                                    clickHandler={
                                        async (setState) => {
                                            setState(false)
                                            let r = await this.MODELS._Station.rentPowerBank(this.state.currentStation.id)

                                            if(r){
                                                Notifications.drawActionResult("pagesNotifications", "Power Bank ejected", "success")
                                                document.getElementById("PowerBank"+index).style.display = "none"
                                            }else {
                                                Notifications.drawActionResult("pagesNotifications", "Could not rent the Power Bank", "error")
                                                setState(true)
                                            }
                                        }
                                    }
                                />
                            </div>
                        )
                    })
                )
            }else {
                return (<div>No Power Banks on the station</div>)
            }
        }else {
            return (<div>Fetching power banks data</div>)
        }

    }
    drawAPN(){
        this.MODELS.station.queryAPNNs(this.state.currentStation.id, "00").then((result)=>{
            if(result != false){
                return <APNView APN={result}></APNView>
            }else {
                Notifications.drawActionResult(
                    "pagesNotifications",
                    "Could not load APN info",
                    "error",
                    "standard",
                    null)
            }
        })
    }
    render() {
        return (
            <YitFragment
                ref={this.currentPage}
                customClass={"StationDetailsFragment"}
                customId={"StationDetailsFragment"}
                title={"Station Details"}
            >
                <div className="globalInformationHolder">
                    <div className="infoEntry">
                        <b>System Id:</b> {this.state.currentStation.systemId}
                    </div>
                    <div className="infoEntry">
                        <b>Public Id:</b>
                        {this.state.currentStation.id}
                    </div>
                    <div className="infoEntry">
                        <b>Total Power Banks:</b>
                        1
                    </div>
                    <div className="infoEntry">
                        <b>Partner:</b>
                        {this.state.currentStation.Partner.fullName}
                    </div>
                </div>
                <YitView
                    ref={this.myRef}
                    className="informationHolder"
                    active={true}
                    anActiveContent={
                        <React.Fragment>
                            <b>Station offline</b>
                            <Button
                                variant="contained"
                                color={"primary"}
                                onClick={()=>{
                                    this.myRef.current.setActive(true)
                                    this.loadStationInfo(this.props.id).then(async ()=>{
                                        //await this.loadPowerBanksInfo(this.state.currentStation.id)
                                        await this.loadAPN(this.state.currentStation.id, 0)
                                    })
                                }}>
                                Try Again
                            </Button>
                        </React.Fragment>
                    }
                >
                    <TabsWidget
                        tabsList={[
                            {
                                title: "Power Banks",
                            },
                            {
                                title: "Access point names",
                            },
                            {
                                title: "Latest Transactions",
                            }
                        ]}>
                        <div id={"powerBanksHolder"}>
                            {this.drawPowerBanks(this.state.currentStationInfo.powerBanksList)}
                        </div>
                        <div id={"APPNSHolder"}>
                            <TabsWidget
                                tabsList={[
                                    {
                                        title: "First",

                                    },
                                    {
                                        title: "Second",
                                    },
                                    {
                                        title: "Third",
                                    },
                                    {
                                        title: "Forth",
                                    },
                                    {
                                        title: "Fifth",
                                    },

                                ]}>
                                <div id="firstHolder"><APNView APN={this.state.stationAPNs[0]}/></div>
                                <div id="secondHolder"><APNView APN={this.state.stationAPNs[1]}/></div>
                                <div id="thirdHolder"><APNView APN={this.state.stationAPNs[2]}/></div>
                                <div id="forthHolder"><APNView APN={this.state.stationAPNs[3]}/></div>
                                <div id="fifthHolder"><APNView APN={this.state.stationAPNs[4]}/></div>
                            </TabsWidget>
                        </div>
                    </TabsWidget>
                </YitView>
            </YitFragment>
        )
    }
}

export default StationDetails

