import * as React from 'react';
import ReactDOM from 'react-dom';


import "./LoginPage.css"


import {Link} from "react-router-dom";
import _Admin from "../../Models/_Admin";
import {LOCALSTORAGE} from "../../Apis/GlobalConfig";
import Alert from "@mui/material/Alert";
import {authOperations} from "../../Apis/authOperations";
import Box from "@mui/material/Box";
import TextField from "@material-ui/core/TextField";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Typography from "@mui/material/Typography";

function Copyright(props) {
    return (
        <Typography variant="body2" color="text.secondary" align="center" {...props}>
            {'Copyright  '}
            <Link color="inherit" href="https://material-ui.com/">
                CBC ALTEC
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}
function validateLogin(mail, admin, token){
    LOCALSTORAGE.setItem("adminMail", mail)
    LOCALSTORAGE.setItem("admin", admin)
    LOCALSTORAGE.setItem("adminToken", token)
    ReactDOM.render(
        <Alert  severity="success" onClose={() => {}}>Login success, redirecting...</Alert>,
        document.getElementById("resultContainer"))
        setTimeout(function(){ window.location.assign("/DashBoard") }, 1500);
}

export default function LoginPage() {
    const handleSubmit = async (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        // eslint-disable-next-line no-console
        let mail = data.get("email")
        let password = data.get("password")
        let loginResult = await _Admin.login({mail , password})
        if(loginResult !== undefined){
            if(loginResult.finalResult === true){
                validateLogin(mail, loginResult.admin, loginResult.token)
            }else {
                ReactDOM.render(
                    <Alert  severity="error" onClose={() => {}}>{loginResult.error}</Alert>,
                    document.getElementById("resultContainer"))
            }
        }else {
            ReactDOM.render(
                <Alert  severity="error" onClose={() => {}}>Unknown error occurred</Alert>,
                document.getElementById("resultContainer"))
        }

    };
    authOperations.isLoggedIn() ?   window.location.assign("/Dashboard/Dashboard") : void(0)

    return (
            <section className={"LoginPage"}>
                <div className="leftSide" style={{
                    backgroundImage: 'url(https://source.unsplash.com/random)',
                    backgroundRepeat: 'no-repeat',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                }}>
                </div>
                <div className="rightSide">
                    <div className="resultContainer" id="resultContainer">

                    </div>

                    <Box
                        sx={{
                            my: 8,
                            mx: 4,
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'center',
                            backgroundColor: "grey"
                        }}
                    >
                            Sign in
                        <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 1 }}>
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                id="email"
                                label="Email Address"
                                name="email"
                                autoComplete="email"
                                autoFocus
                            />
                            <TextField
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Password"
                                type="password"
                                id="password"
                                autoComplete="current-password"
                            />
                            <FormControlLabel
                                control={<Checkbox value="remember" color="primary" />}
                                label="Remember me"
                            />
                            <Button
                                color={"primary"}
                                type="submit"
                                fullWidth
                                variant="contained"
                                sx={{ mt: 3, mb: 2 }}
                            >
                                Sign In
                            </Button>
                            <Grid container>
                                <Grid item xs>
                                    <Link href="#" variant="body2">
                                        Forgot password?
                                    </Link>
                                </Grid>
                                <Grid item>
                                    <Link href="#" variant="body2">
                                        {"Don't have an account? Sign Up"}
                                    </Link>
                                </Grid>
                            </Grid>

                            <Copyright sx={{ mt: 5 }} />
                        </Box>
                    </Box>
                </div>

            </section>
    );
}